.modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 120%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-container-open {
  position: relative;
  border: solid;
  border-color: #66bb6a;
  border-radius: 20px;
  width: 70%;
  left: 15%;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 10px;
  background-color: white;
}

.modal-container-open-buttonEdit {
  position: relative;
  padding-right: 46px;
  border-radius: 20px;
  background-color: #66bb6a;
}
.modal-container-open-buttonEdit::before {
  content: "";
  position: absolute;
  right: 6px;
  background-color: #cae6b7;
  width: 20px;
  height: 20px;
  border-radius: 20px;
}
.modal-container-open-buttonEdit::after {
  content: "";
  position: absolute;
  right: 13px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);
}
.modal-container-open-buttonEdit:hover {
  background-color: #39993c;
}
.modal-container-open-buttonAdd {
  position: relative;
  padding-right: 46px;
  border-radius: 20px;
  background-color: #66bb6a;
  margin-left: 8px;
}
.modal-container-open-buttonAdd::before {
  content: "";
  position: absolute;
  right: 6px;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: #cae6b7;
}
.modal-container-open-buttonAdd::after {
  content: "";
  position: absolute;
  right: 13px;
  width: 6px;
  height: 6px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
  transform: rotate(45deg);
}
.modal-container-open-buttonAdd:hover {
  background-color: #39993c;
}
.modal-container-open-buttonReturn {
  position: relative;
  padding-right: 60px;
  border-radius: 20px;
  margin: 10px;
  background-color: #66bb6a;
}
.modal-container-open-buttonReturn::before {
  content: "";
  position: absolute;
  right: 7px;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: #cae6b7;
}
.modal-container-open-buttonReturn::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);
  right: 15px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
}
.modal-container-open-buttonReturn:hover {
  background-color: #39993c;
}

.modal-container-edit {
  margin: 0 auto;
  max-width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.modal-container-edit-buttonDelete {
  position: relative;
  padding-right: 42px;
  border-radius: 20px;
  margin: 10px;
  background-color: #66bb6a;
}
.modal-container-edit-buttonDelete::before {
  content: "";
  position: absolute;
  right: 7px;
  background-color: #cae6b7;
  width: 20px;
  height: 20px;
  border-radius: 20px;
}
.modal-container-edit-buttonDelete::after {
  content: "";
  position: absolute;
  right: 14px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
  width: 6px;
  height: 6px;
  transform: rotate(45deg);
}
.modal-container-edit-buttonDelete:hover {
  background-color: #39993c;
}
.modal-container-edit-elements {
  border: solid;
  border-color: #66bb6a;
  border-radius: 15px;
  padding: 15px;
  margin: 10px;
}
.modal-container-edit-elements ul {
  text-align: left;
}
.modal-container-add {
  border: solid;
  border-color: #66bb6a;
  border-radius: 15px;
  padding: 15px;
  margin: 10px;
}
.modal-container-add-buttonAdd {
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding-right: 60px;
  border-radius: 20px;
  margin: 20px 10px 10px 10px;
  background-color: #66bb6a;
}
.modal-container-add-buttonAdd::before {
  content: "";
  position: absolute;
  right: 7px;
  width: 20px;
  height: 20px;
  background-color: #cae6b7;
  border-radius: 20px;
}
.modal-container-add-buttonAdd::after {
  content: "";
  position: absolute;
  right: 15px;
  transform: rotate(45deg);
  width: 5px;
  height: 5px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
}
.modal-container-add-buttonAdd:hover {
  background-color: #39993c;
}

.modal-container-add-elements {
  justify-content: center;
  align-items: center;
  display: flex;
}
.modal-container-add-elements-form div {
  display: flex;
}
